.button {
  width: 120px;
  padding: 15px 5px;
  background-color: #ffffff;
  font-size: 18px;
  font-weight: 700;
  border-radius: 5px;
  border: 1px solid #acacac;
  box-shadow: 0px 2px 6px -2px rgba(0, 0, 0, 0.2);
  outline: none;
  transition: all 0.5s ease;
  cursor: pointer;
}

.button:hover {
  background-color: #fdad00;
}
